@media screen and (min-width: 1380px) {
	.background {
		background-image: url("../../assets/images/man-posing-dark-fedora-hat-600nw-699567733.webp");
		max-width: 100%;
		height: 700px;
		background-position: top center;
		overflow: hidden;
		background-size: cover;
		background-repeat: no-repeat;
		background-position: center center;
	}

	.container {
		box-sizing: border-box;
		display: flex;
		flex-direction: column;
		justify-content: center;
		align-items: center;
	}

	.form {
		display: flex;
		flex-direction: column;
		align-items: flex-start;
		align-self: stretch;
		margin-top: 2rem;
		margin-left: 0.5rem;
	}

	.h1 {
		font-size: 63px;
		letter-spacing: 0.64px;
	}

	.h2 {
		font-size: 28px;
		letter-spacing: 3.77px;
	}

	.button {
		margin-top: 48px;
	}

	.button_mobile {
		display: none;
	}
}

