.h1 {
	color: #fff;
	text-align: center;
	font-family: Geologica;
	font-style: normal;
	font-weight: 400;
	line-height: 140%;
	text-transform: uppercase;
	margin: 0%;
}

.h2 {
	color: #e9c394;
	text-align: center;
	font-family: Geologica;
	font-style: normal;
	font-weight: 300;
	line-height: 140%;
	text-transform: uppercase;
	margin: 0%;
}

.name {
	display: flex;
	flex: 1 0 0;
	height: 64px;
	align-items: center;
	gap: 16px;
	align-self: stretch;
	background: transparent;
	font-family: Geologica;
	font-size: 16px;
	font-style: normal;
	font-weight: 400;
	line-height: 140%;
	color: rgba(255, 255, 255, 0.5);
	border: none;
	border-bottom: 1px solid rgba(255, 255, 255, 0.5);
}

.name::placeholder {
	color: #fff;
}

.name:hover {
	border-bottom: 1px solid rgba(255, 255, 255);
	cursor: pointer;
}

input:focus {
	outline: none !important;
}

input:focus::placeholder {
	color: rgba(255, 255, 255, 0.5);
}

input:-webkit-autofill,
input:-webkit-autofill:hover,
input:-webkit-autofill:focus,
input:-webkit-autofill:active {
	-webkit-background-clip: text;
	-webkit-text-fill-color: #ffffff;
	transition: background-color 5000s ease-in-out 0s;
	box-shadow: inset 0 0 20px 20px #23232329;
}

.number {
	display: flex;
	flex: 1 0 0;
	height: 64px;
	align-items: center;
	gap: 16px;
	align-self: stretch;
	background: transparent;
	font-family: Geologica;
	font-size: 16px;
	font-style: normal;
	font-weight: 400;
	line-height: 140%;
	color: rgba(255, 255, 255);
	border: none;
	border-bottom: 1px solid rgba(255, 255, 255, 0.5);
}

.number::placeholder {
	color: #fff;
}

.number:hover {
	border-bottom: 1px solid rgba(255, 255, 255);
	cursor: pointer;
}

.question {
	display: flex;
	flex: 1 0 0;
	height: 64px;
	align-items: center;
	gap: 16px;
	align-self: stretch;
	background: transparent;
	font-family: Geologica;
	font-size: 16px;
	font-style: normal;
	font-weight: 400;
	line-height: 140%;
	color: rgba(255, 255, 255, 0.5);
	border: none;
	border-bottom: 1px solid rgba(255, 255, 255, 0.5);
}

.question::placeholder {
	color: #fff;
}

.question:hover {
	border-bottom: 1px solid rgba(255, 255, 255);
	cursor: pointer;
}

.button {
	display: flex;
	height: 56px;
	padding: 16px;
	justify-content: center;
	align-items: flex-start;
	gap: 8px;
	border-radius: 40px;
	background: #fff;
	color: #403531;
	font-family: Geologica;
	font-size: 16px;
	font-style: normal;
	font-weight: 500;
	line-height: 140%;
}

.button:hover {
	background: #fff2d8;
	cursor: pointer;
	transition-duration: 0.3s;

	.vector {
		& path {
			fill: #403531;
			transition-duration: 0.3s;
		}
	}
}

.wrapper {
	display: flex;
	align-items: center;
	align-self: stretch;
	flex-direction: row;
}

.icon {
	height: 24px;
	width: 24px;
}

.p {
	background-color: #fff;
	color: #000;
	font-family: Geologica;
	font-size: 16px;
	font-style: normal;
	font-weight: 400;
	line-height: 140%;
	height: 26px;
}
